import pnp from "sp-pnp-js";
import { Web } from "sp-pnp-js/lib/sharepoint/webs";

angular.module('compassionIntranet').service('rssFeedService', ['$http', '$q', 'COM_CONFIG', function ($http, $q, COM_CONFIG) {
    function getRssItems(user) {
        var defer = $q.defer();
        let web = new Web(COM_CONFIG.rootWeb);
        web.lists.getByTitle(COM_CONFIG.lists.userRssFeedsListTitle).items
            .filter("COM_RssFeedUserId eq '" + user + "'")
            .get()
            .then(function(data){
                var promises = new Array();
                for(var i = 0; data.length > i; i++)
                {
                    var p = getRssUrl(data[i].Id);
                    promises.push(p)
                }
                $q.all(promises).then(function(response){
                    defer.resolve(response);
                });                
            });

        return defer.promise;
    }
    function getRssUrl(id) {
        var defer = $q.defer();
        let web = new Web(COM_CONFIG.rootWeb);
        web.lists.getByTitle(COM_CONFIG.lists.rssFeedsListTitle).items
            .getById(id)
            .get()
            .then(function(item){ 
                defer.resolve(item.COM_RssFeedUrl.Url); 
            });

        return defer.promise;
    }
    function getRssFeeds(feeds) {
        var defer = $q.defer(),
            result = [];;
        for(var i = 0; feeds.length > i; i++)
        {
            $http.jsonp('//ajax.googleapis.com/ajax/services/feed/load?v=1.0&num=50&callback=JSON_CALLBACK&q=' + encodeURIComponent(feeds[i]))
                .then(function(response) {
                    if (!response.data.responseData) {
                        return CommonSrv.handleError('Unable to fetch RSS feed from provided URL. Please check the URL.');
                    }
                    var feeds = response.data.responseData.feed.entries;
                    
                    for (var i = 0; i < count; ++i) {
                        var f = feeds[i];
                        f.publishedDate = new Date(f.publishedDate).toISOString();
                        result.push(f);
                    }                    
                }, CommonSrv.handleError);
        }
        deffered.resolve(result);
        return defer.promise;
    }
    this.getMyRssFeeds = function (user) {
        var defer = $q.defer();
        getRssItems(user)
            .then(function(feeds){
                getRssFeeds(feeds)
                    .then(function(feedContent){
                        defer.resolve(feedContent);
                    });
            });

        return defer.promise;        
    }
}]
);


